* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --clr-primary: #2680c0;
  --clr-primary-dark: hsl(205, 100%, 21%);
  --clr-primary-light: hsl(205, 84%, 85%);
  --black: #000000;
  --main: #181818;
  --blue: #21aaff;
  --azure: #026ae7;
  --azure-telegram: #1e99e6;
  --azure-hover: #025fd0;
  --azure-light: #c0e7ff;
  --green: #00a17e;
  --green-300: #35d77e;
  --green-hover: #32c072;
  --dark-green: #4a805c;
  --yellow: #fde181;
  --yellow-300: #c2aa4d;
  --white: #ffffff;
  --black-04: rgba(0, 0, 0, 0.04);
  --black-08: rgba(0, 0, 0, 0.08);
  --red-300: #fe495c;
  --dark-red: #ff3366;
  --red-hover: #e04051;
  --orange: #fc8c3c;
  --orange-300: #fc8c3c;
  --orange-hover: #de7b35;
  --orange-20: rgba(252, 140, 60, 0.2);
  --yellow: #ffd62c;
  --yellow-hover: #e0b400;
  --grey: #65676b;
  --yellow-300: #ffcc00;
  --gray-base-2: #a0a1a3;
  --gray-base-7: #f8f8fa;
  --gray-base-8: #f4f5f7;
  --dark-gray-1: #a8abb0;
  --dark-gray-2: #616162;
  --dark-gray-3: #333435;
  --dark-gray-4: #313132;
  --dark-gray-5: #2c2c2d;
  --dark-gray-6: #292a2a;
  --dark-gray-7: #232425;
  --dark-gray-8: #202021;
  --dark-gray-9: #181818;
  --light-gray-1: #65676b;
  --light-gray-2: #a0a1a3;
  --light-gray-3: #eaebee;
  --light-gray-4: #eeeff1;
  --light-gray-5: #e2e3e4;
  --light-gray-6: #dddee0;
  --light-gray-7: #ecedee;
  --light-gray-8: #f8f8fa;
  --light-gray-9: #181818;
  --light-gray-10: #f4f5f7;
  --lightShadow: 2px 5px 3px 0px rgba(187, 186, 186, 0.5);
  --darkShadow: 4px 10px 5px 0px rgba(0, 0, 0, 0.5);
  --mainSpacing: 3px;
  --mainTransition: all 0.3s linear;
  --mainBlack: #222;
}

html,
body {
  /* overflow: hidden; */
  height: 100%;
}

body {
  color: var(--mainBlack);
  line-height: 1.4;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 16px;
  /* font-family: "Open Sans", sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

